import timeline from '../timeline.js';
import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import tools from '../tools.js';
import { randomNumber } from '../utils.js';
import { LAYERS, JUICE_CONFIG } from '../config-defaults.js';


class Juice {
    dur      = JUICE_CONFIG.duration;
    anim     = tween.exponential.co;
    dropAnim = tween.quadratic.co;
    shape;
    figure;

    constructor (x, y, color, shape = JUICE_CONFIG.shapes.CIRCLE) {
        this.originX  = x;
        this.originY  = y;
        this.color    = color;
        this.radius   = JUICE_CONFIG.radius;
        this.distance = randomNumber(200) + 100;
        this.dir      = randomNumber(360) * Math.PI / 180;
        this.shape    = shape;
    }

    render () {
        const paper = layer.getLayer(LAYERS.JUICE);

        if (this.shape === JUICE_CONFIG.shapes.CIRCLE) {
            this.figure = paper.circle(
                this.originX,
                this.originY,
                this.radius,
            ).attr({
                fill  : this.color,
                stroke: 'none',
            });
        } else if (this.shape === JUICE_CONFIG.shapes.SQUARE) {
            this.figure = paper.rect(
                this.originX - this.radius,
                this.originY - this.radius,
                this.radius * 2,
                this.radius * 2,
            ).attr({
                fill  : this.color,
                stroke: 'none',
            });
        } else if (this.shape === JUICE_CONFIG.shapes.DIAMOND) {
            const starPath = this.createStarPath(this.originX, this.originY, this.radius, 4);
            this.figure    = paper.path(starPath).attr({
                fill  : this.color,
                stroke: 'none',
            });
        } else if (this.shape === JUICE_CONFIG.shapes.STAR) {
            const starPath = this.createStarPath(this.originX, this.originY, this.radius, 5);
            this.figure    = paper.path(starPath).attr({
                fill  : this.color,
                stroke: 'none',
            });
        }
    }

    createStarPath (cx, cy, r, points) {
        const step = Math.PI / points;
        let path   = '';
        for (let i = 0; i < 2 * points; i++) {
            const angle  = i * step;
            const radius = i % 2 === 0 ? r : r / 2;
            const x      = cx + radius * Math.sin(angle);
            const y      = cy - radius * Math.cos(angle);
            path += i === 0 ? `M ${ x } ${ y }` : `L ${ x } ${ y }`;
        }
        path += 'Z';
        return path;
    }

    sputter () {
        timeline.createTask({
            start       : 0,
            duration    : this.dur,
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeEnd   : this.onTimeEnd,
        });
    }

    onTimeUpdate (time) {
        const distance = this.anim(time, 0, this.distance, this.dur);
        const x        = this.originX + distance * Math.cos(this.dir);
        const y        = this.originY + distance * Math.sin(this.dir) + this.dropAnim(time, 0, 200, this.dur);
        const z        = this.anim(time, 1, -1, this.dur);

        if (this.shape === JUICE_CONFIG.shapes.CIRCLE) {
            this.figure.attr({ cx: x, cy: y }).scale(z, z);
        } else if (this.shape === JUICE_CONFIG.shapes.SQUARE) {
            this.figure.attr({ x: x - this.radius * z, y: y - this.radius * z }).scale(z, z);
        } else if (this.shape === JUICE_CONFIG.shapes.DIAMOND) {
            this.figure.attr({ path: this.createStarPath(x, y, this.radius * z, 4) }).scale(z, z);
        } else if (this.shape === JUICE_CONFIG.shapes.STAR) {
            this.figure.attr({ path: this.createStarPath(x, y, this.radius * z, 5) }).scale(z, z);
        }
    }

    onTimeEnd () {
        this.figure.remove();
        tools.unsetObject(this);
    }
}


class JuiceFactory {
    num = JUICE_CONFIG.number;

    create (x, y, color) {
        for (let i = 0; i < this.num; i++) {
            this.createOne(x, y, color);
        }
    }

    createOne = function (x, y, color) {
        if (!color) return;

        const juice = new Juice(x, y, color, JUICE_CONFIG.shape);
        juice.render();
        juice.sputter();
    }
}


export default new JuiceFactory();
