import soundFactory from './factory/sound.js';

import message from './message.js';
import timeline from './timeline.js';
import state from './state.js';
import game from './game.js';
import layer from './layer.js';

import background from './object/background.js';
import homeMask from './object/home-mask.js';
import homeLogo from './object/home-logo.js';
import homeText from './object/home-text.js';
import homeButton from './object/home-button.js';
import resultLogo from './object/result-logo.js';
import resultText from './object/result-text.js';
import resultButtonNext from './object/result-button-next.js';
import resultButtonReplay from './object/result-button-replay.js';
import score from './object/score.js';
import lose from './object/lose.js';
import bottom from './object/bottom.js';
import gameOver from './object/game-over.js';
import flash from './object/flash.js';
import fps from './object/fps.js';
import soundButton from './object/sound-button.js';
import brandQuestion from './object/brand-question.js';
import brandIntro from './object/brand-intro.js';
import { LAYERS, MESSAGES, MODES, SCENE_STATES, SCENES, SOUNDS, STATE_KEYS } from './config-defaults.js';


class Scene {
    menuSnd;
    gameStartSnd;
    setTimeout  = timeline.setTimeout.bind(timeline);
    setInterval = timeline._setInterval.bind(timeline);

    constructor () {
        message.addEventListener(MESSAGES.SCENE_SWITCH_SCENE, (name) => {
            this.switchScene(name);
        });

        message.addEventListener(MESSAGES.TOGGLE_SOUND, () => {
            this.toggleSound();
        });
    }

    init () {
        this.menuSnd      = soundFactory.create(SOUNDS.MENU);
        this.gameStartSnd = soundFactory.create(SOUNDS.START);
        [
            background,
            homeMask,
            homeLogo,
            homeText,
            homeButton,
            score,
            lose,
            gameOver,
            flash,
            bottom,
            resultLogo,
            resultText,
            resultButtonNext,
            resultButtonReplay,
            soundButton,
            brandQuestion,
            brandIntro,
            fps,
        ].invoke('set');

        this.setInterval(fps.update.bind(fps), 500);
    }

    switchScene (name) {
        const onShow     = () => sceneState.set(SCENE_STATES.READY);
        const curScene   = state(STATE_KEYS.SCENE_NAME);
        const sceneState = state(STATE_KEYS.SCENE_STATE);

        if (curScene.is(name)) return;

        const onHide = () => {
            curScene.set(name);
            sceneState.set(SCENE_STATES.ENTERING);
            switch (name) {
                case SCENES.HOME_MENU:
                    this.showMenu(onShow);
                    break;
                case SCENES.GAME_BODY:
                    this.showGame(onShow);
                    break;
                case SCENES.GAME_OVER:
                    this.showGameOver(onShow);
                    break;
                case SCENES.GAME_RESULT:
                    this.showResult(onShow);
                    break;
                case SCENES.BRAND_INTRO:
                    this.showBrandIntro(onShow);
                    break;
                case SCENES.BRAND_QUESTION:
                    this.showBrandQuestion(onShow);
                    break;
            }
        };

        sceneState.set(SCENE_STATES.EXITING);

        if (curScene.isunset()) onHide();
        else if (curScene.is(SCENES.HOME_MENU)) this.hideMenu(onHide);
        else if (curScene.is(SCENES.GAME_BODY)) this.hideGame(onHide);
        else if (curScene.is(SCENES.GAME_OVER)) this.hideGameOver(onHide);
        else if (curScene.is(SCENES.GAME_RESULT)) this.hideResult(onHide);
        else if (curScene.is(SCENES.BRAND_INTRO)) this.hideBrandIntro(onHide);
        else if (curScene.is(SCENES.BRAND_QUESTION)) this.hideBrandQuestion(onHide);
    }

    toggleSound () {
        const curScene = state(STATE_KEYS.SCENE_NAME);
        const method   = soundButton.isOn ? 'play' : 'stop';

        if (curScene.is(SCENES.HOME_MENU)) this.menuSnd[method]();
        else if (curScene.is(SCENES.GAME_BODY)) this.gameStartSnd[method]();
        else if (curScene.is(SCENES.GAME_RESULT)) this.menuSnd[method]();
    }

    showMenu (callback) {
        layer.showLayer(LAYERS.START_SCREEN);

        const group = [
            [ homeMask, 0 ],
            [ homeLogo, 0 ],

            [ homeText, 1000 ],
            [ bottom, 1000 ],

            [ homeButton, 1200 ],
        ];

        group.invoke(MODES.SHOW);

        this.menuSnd.play();

        soundButton.showHomeSoundButton(1200);

        this.setTimeout(callback, 2500);
    }

    hideMenu (callback) {
        [ homeButton, homeMask, homeLogo, homeText ].invoke(MODES.HIDE);

        soundButton.hideHomeSoundButton();

        this.menuSnd.stop();

        this.setTimeout(() => {
            callback();
            layer.hideLayer(LAYERS.START_SCREEN);
        }, 1200);
    }

    showGame (callback) {
        layer.showLayer(LAYERS.FRUIT);

        score.show();
        lose.show();
        game.start();

        this.gameStartSnd.play();

        soundButton.showGameSoundButton();

        this.setTimeout(callback, 700);
    }

    hideGame (callback) {
        score.hide();
        lose.hide();

        soundButton.hideGameSoundButton();

        this.gameStartSnd.stop();

        this.setTimeout(() => {
            callback();
            layer.hideLayer(LAYERS.FRUIT);
        }, 700);
    }

    showGameOver (callback) {
        const group = [
            [ gameOver, 0 ],
        ];

        group.invoke(MODES.SHOW);

        this.setTimeout(() => {
            callback();
            this.switchScene(SCENES.GAME_RESULT);
        }, 2000);
    }

    hideGameOver (callback) {
        [ gameOver ].invoke(MODES.HIDE);

        this.setTimeout(callback, 1000);
    }

    showResult () {
        layer.showLayer(LAYERS.RESULT_SCREEN);

        const group = [
            [ resultLogo, 0 ],
            [ resultText, 700 ],
            [ resultButtonNext, 1000 ],
            [ resultButtonReplay, 1000 ],
        ];

        group.invoke(MODES.SHOW);

        this.menuSnd.play();

        soundButton.showResultSoundButton();
    }

    hideResult (callback) {
        [ resultLogo, resultText, resultButtonNext, resultButtonReplay ].invoke(MODES.HIDE);

        soundButton.hideResultSoundButton();

        this.menuSnd.stop();

        this.setTimeout(() => {
            callback();
            layer.hideLayer(LAYERS.RESULT_SCREEN);
        }, 1000);
    }

    showBrandIntro () {
        layer.showLayer(LAYERS.BRAND_INTRO);

        const group = [
            [ brandIntro, 0 ],
        ];

        group.invoke(MODES.SHOW);

        soundButton.showResultSoundButton();
    }

    hideBrandIntro (callback) {
        [ brandIntro ].invoke(MODES.HIDE);

        soundButton.hideResultSoundButton();

        this.setTimeout(() => {
            callback();
            layer.hideLayer(LAYERS.BRAND_INTRO);
        }, 1000);
    }

    showBrandQuestion () {
        layer.showLayer(LAYERS.BRAND_QUESTION);

        const group = [
            [ brandQuestion, 0 ],
        ];

        group.invoke(MODES.SHOW);

        soundButton.showResultSoundButton();
    }

    hideBrandQuestion (callback) {
        [ brandQuestion ].invoke(MODES.HIDE);

        soundButton.hideResultSoundButton();

        this.setTimeout(() => {
            callback();
            layer.hideLayer(LAYERS.BRAND_QUESTION);
        }, 1000);
    }
}


export default new Scene();
