import Ucren from './libs/ucren.js';
import { makeElement } from './utils.js';
import { LAYERS, FONT_FAMILY } from './config-defaults.js';
import viewport from './layout/viewport.js';


const layers            = {};
const zIndexes          = {
    [LAYERS.PRELOAD]       : zi(),
    [LAYERS.DEFAULT]       : zi(),
    [LAYERS.FLASH]         : zi(),
    [LAYERS.JUICE]         : zi(),
    [LAYERS.KNIFE]         : zi(),
    [LAYERS.FRUIT]         : zi(),
    [LAYERS.LIGHT]         : zi(),
    [LAYERS.FRUIT]         : zi(),
    [LAYERS.MASK]          : zi(),
    [LAYERS.START_SCREEN]  : zi(),
    [LAYERS.RESULT_SCREEN] : zi(),
    [LAYERS.BRAND_INTRO]   : zi(),
    [LAYERS.BRAND_QUESTION]: zi(),
};
const { width, height } = viewport;

function createImage (layer, src, x, y, w, h, preserveAspectRatio = 'none') {
    layer = this.getLayer(layer);
    return layer.image(src, x, y, w, h, preserveAspectRatio);
}

function createText (layer, text, x, y, fill, size, weight = 'normal', textAnchor = 'start') {
    layer = this.getLayer(layer);

    return layer.text(x, y, text).attr({
        fill         : fill || '#fff',
        'font-family': FONT_FAMILY,
        'font-size'  : size || '14px',
        'font-weight': weight,
        'text-anchor': textAnchor,
    });
}

function getLayer (name) {
    name  = name || LAYERS.DEFAULT;
    let p = layers[name], layer;

    if (p) {
        return p;
    } else {
        layer = makeElement('div', {
            'class'    : 'layer',
            'data-name': name,
            'style'    : 'z-index: ' + (zIndexes[name] || 0) + ';',
        });
        Ucren.Element('extra').add(layer);
        p = layers[name] = Raphael(layer, width, height);
        return p;
    }
}

function hideLayer (name) {
    const layer = getLayer(name);
    layer && layer.canvas && layer.canvas.parentNode && (layer.canvas.parentNode.style.display = 'none');
}

function showLayer (name) {
    const layer = getLayer(name);
    layer && layer.canvas && layer.canvas.parentNode && (layer.canvas.parentNode.style.display = 'block');
}

function zi () {
    return zi.num = ++zi.num || 2;
}

export default {
    createImage,
    createText,
    getLayer,
    hideLayer,
    showLayer,
};
