import layer from '../layer.js';
import timeline from '../timeline.js';
import { LAYERS, KNIFE_PART_CONFIG } from '../config-defaults.js';


class Knifes {
    _knifes     = [];
    lastX       = null;
    lastY       = null;
    switchState = true;

    _anims = [];

    get anims () {
        return this._anims;
    }

    newKnife () {
        this.lastX = null;
        this.lastY = null;
    }

    through (x, y) {
        if (!this.switchState) return;

        let ret = null;

        if (this.lastX !== null && (this.lastX !== x || this.lastY !== y)) {
            new KnifePart({
                sx: this.lastX,
                sy: this.lastY,
                ex: x,
                ey: y,
            }).set();

            ret = [ this.lastX, this.lastY, x, y ];
        }

        this.lastX = x;
        this.lastY = y;

        return ret;
    }

    pause () {
        this.anims.clear && this.anims.clear();
        this.switchOff();
    }

    switchOff () {
        this.switchState = false;
    }

    switchOn () {
        this.switchState = true;
        this.endAll();
    }

    endAll () {
        for (let i = this._knifes.length - 1; i >= 0; i--) {
            this._knifes[i].end();
        }
    }

    push (knife) {
        this._knifes.push(knife);
    }

    indexOf (knife) {
        return this._knifes.indexOf(knife);
    }

    splice (index, count) {
        this._knifes.splice(index, count);
    }
}


const knifes = new Knifes();


class KnifePart {
    color  = KNIFE_PART_CONFIG.color;
    stroke = KNIFE_PART_CONFIG.stroke;
    life   = KNIFE_PART_CONFIG.life;

    constructor (config) {
        this.sx = config.sx;
        this.sy = config.sy;
        this.ex = config.ex;
        this.ey = config.ey;

        knifes.push(this);
    }

    set () {
        const knifeLayer = layer.getLayer(LAYERS.KNIFE);
        let sx, sy, ex, ey, dx, dy, ax, ay;

        sx = this.sx;
        sy = this.sy;
        ex = this.ex;
        ey = this.ey;

        dx = sx - ex;
        dy = sy - ey;
        ax = Math.abs(dx);
        ay = Math.abs(dy);

        if (ax > ay) {
            sx += dx < 0 ? -1 : 1;
            sy += dy < 0 ? -(ay / ax) : ay / ax;
        } else {
            sx += dx < 0 ? -(ax / ay) : ax / ay;
            sy += dy < 0 ? -1 : 1;
        }

        this.line = knifeLayer.path(
            'M' + sx + ',' + sy + 'L' + ex + ',' + ey,
        ).attr({
            'stroke'      : this.color,
            'stroke-width': this.stroke + 'px',
        });

        timeline.createTask({
            start       : 0,
            duration    : this.life,
            object      : this,
            onTimeUpdate: this.update,
            onTimeEnd   : this.end,
            recycle     : knifes.anims,
        });

        return this;
    }

    update (time) {
        this.line.attr(
            'stroke-width',
            this.stroke * (1 - time / this.life) + 'px',
        );
    }

    end () {
        this.line.remove();

        const index = knifes.indexOf(this);
        if (index) knifes.splice(index, 1);
    }
}


export default knifes;
