import Ucren from './libs/ucren.js';


function postMessage (message/*, message, message... */, to) {
    const messages   = [].slice.call(arguments, 0);
    const splitIndex = messages.length - 1;

    to = messages[splitIndex];
    messages.slice(0, splitIndex);

    Ucren.dispatch(to, messages);
}

/**
 * bind a message handler
 * @param {String}   from    message address
 * @param {Function} fn    message handler
 */
function addEventListener (from, fn) {
    Ucren.dispatch(from, fn);
}


export default {
    postMessage,
    addEventListener,
};
