import { FRUIT_CONFIG, LOSE_CONFIG } from './config-defaults.js';
import preloadFactory from './factory/preload.js';

function getURLs () {
    return [
        Object.values(FRUIT_CONFIG.infos).map(([ src ]) => src),
        Object.values(FRUIT_CONFIG.cutImages).flat(),
        FRUIT_CONFIG.shadowImage,
        LOSE_CONFIG.conf.map(({ src }) => src),
    ].flat();
}

function preload () {
    const URLs = getURLs();

    URLs.forEach(preloadFactory.create);
}

export default preload;
