import preload from './preload.js';
import tools from './tools.js';
import message from './message.js';
import state from './state.js';
import timeline from './timeline.js';
import collide from './collide.js';
import control from './control.js';
import game from './game.js';
import scene from './scene.js';
import csl from './object/console.js';
import { MESSAGES, SCENES, STATE_KEYS, SCENE_STATES } from './config-defaults.js';


class Main {
    setTimeout = timeline.setTimeout.bind(timeline);
    log = (() => {
        let time  = 1e3;
        const add = 300;
        const fn  = function (text) {
            setTimeout(function () {
                csl.log(text);
            }, time);
            time += add;
        };
        fn.clear  = function () {
            setTimeout(csl.clear.bind(csl), time);
            time += add;
        };
        return fn;
    })();

    constructor () {
        window.Telegram?.WebApp?.disableVerticalScroll && window.Telegram.WebApp.disableVerticalScroll(true);

        document.addEventListener('touchstart', (event) => {
            event.preventDefault();
        }, { passive: false });

        document.addEventListener('touchmove', (event) => {
            event.preventDefault();
        }, { passive: false });

        message.addEventListener(MESSAGES.SLICE, knife => {
            const fruits = collide.checkSlice(knife);

            if (fruits.length) {
                const angle = tools.getAngleByRadian(tools.pointToRadian(knife.slice(0, 2), knife.slice(2, 4)));
                fruits.forEach(fruit => message.postMessage(fruit, angle, MESSAGES.SLICE_AT));
            }
        });

        message.addEventListener(MESSAGES.TOUCH, position => {
            const fruits = collide.checkTouch(position);

            if (fruits.length) {
                fruits.forEach(fruit => message.postMessage(fruit, MESSAGES.TOUCH_AT));
            }
        });

        message.addEventListener(MESSAGES.SLICE_AT, (fruit, angle) => {
            if (state(STATE_KEYS.SCENE_STATE).isnot(SCENE_STATES.READY)) {
                return;
            }

            if (state(STATE_KEYS.SCENE_NAME).is(SCENES.GAME_BODY)) {
                game.sliceAt(fruit, angle);
            }
        });

        message.addEventListener(MESSAGES.TOUCH_AT, fruit => {
            if (state(STATE_KEYS.SCENE_STATE).isnot(SCENE_STATES.READY)) {
                return;
            }

            if (state(STATE_KEYS.SCENE_NAME).is(SCENES.GAME_BODY)) {
                game.catchFruit(fruit);
            }
        });

        message.addEventListener(MESSAGES.START_GAME, () => {
            scene.switchScene(SCENES.GAME_BODY);
        });

        preload();
    }

    start () {
        [ timeline, scene, control ].invoke('init');

        this.log.clear();

        this.setTimeout(scene.switchScene.saturate(scene, SCENES.HOME_MENU), 3000);
    }
}


export default new Main();
