import tween from '../libs/tween/index.js';
import layer from '../layer.js';
import message from '../message.js';
import timeline from '../timeline.js';
import { LAYERS, LOSE_CONFIG, MESSAGES, MODES, SCENES } from '../config-defaults.js';
import vibration from './vibration.js';


class Lose {
    anim       = tween.exponential.co;
    back       = tween.back.co;
    o1;
    o2;
    o3;
    info       = [];
    animLength = LOSE_CONFIG.animLength;
    number     = 0;
    anims      = [];

    set () {
        this.o1 = layer.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[0].src,
            LOSE_CONFIG.conf[0].sx,
            LOSE_CONFIG.conf[0].y,
            LOSE_CONFIG.conf[0].w,
            LOSE_CONFIG.conf[0].h,
        ).hide();

        this.o2 = layer.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[1].src,
            LOSE_CONFIG.conf[1].sx,
            LOSE_CONFIG.conf[1].y,
            LOSE_CONFIG.conf[1].w,
            LOSE_CONFIG.conf[1].h,
        ).hide();

        this.o3 = layer.createImage(
            LAYERS.DEFAULT,
            LOSE_CONFIG.conf[2].src,
            LOSE_CONFIG.conf[2].sx,
            LOSE_CONFIG.conf[2].y,
            LOSE_CONFIG.conf[2].w,
            LOSE_CONFIG.conf[2].h,
        ).hide();

        this.info = [
            { image: this.o1, config: LOSE_CONFIG.conf[0] },
            { image: this.o2, config: LOSE_CONFIG.conf[1] },
            { image: this.o3, config: LOSE_CONFIG.conf[2] },
        ]
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : this.animLength,
            data        : [
                MODES.SHOW,
                LOSE_CONFIG.conf[0].sx,
                LOSE_CONFIG.conf[0].ex,
                LOSE_CONFIG.conf[1].sx,
                LOSE_CONFIG.conf[1].ex,
                LOSE_CONFIG.conf[2].sx,
                LOSE_CONFIG.conf[2].ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : this.animLength,
            data        : [
                MODES.HIDE,
                LOSE_CONFIG.conf[0].ex,
                LOSE_CONFIG.conf[0].sx,
                LOSE_CONFIG.conf[1].ex,
                LOSE_CONFIG.conf[1].sx,
                LOSE_CONFIG.conf[2].ex,
                LOSE_CONFIG.conf[2].sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    showLoseAt () {
        const { image, config } = this.info[this.number] || {};
        const shouldUpdate = !!image && image.attr('src') !== config.srcF;

        shouldUpdate && image.attr('src', config.srcF).scale(1e-5, 1e-5);
        shouldUpdate && this.scaleImage(image);
        shouldUpdate && vibration.vibrate();

        this.number++;

        if (this.number === 3) {
            vibration.gameOver();
            this.number = 0;
            timeline.setTimeout(() => {
                message.postMessage(MESSAGES.GAME_OVER);
                message.postMessage(SCENES.GAME_OVER, MESSAGES.SCENE_SWITCH_SCENE);
            }, 1000);
        }
    }

    scaleImage (image) {
        const dur = 500;

        image.myOnScaling = image.myOnScaling || ((time, z) => {
            image.scale(z = this.back(time, 1e-5, 1 - 1e-5, dur), z);
        });

        image.myOnScaleEnd = image.myOnScaleEnd || function () {
            image.scale(1, 1);
        };

        timeline.createTask({
            start       : 0,
            duration    : dur,
            object      : image,
            onTimeUpdate: image.myOnScaling,
            onTimeEnd   : image.myOnScaleEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time, mode, x1s, x1e, x2s, x2e, x3s, x3e) {
        this.o1.attr('x', this.anim(time, x1s, x1e - x1s, this.animLength));
        this.o2.attr('x', this.anim(time, x2s, x2e - x2s, this.animLength));
        this.o3.attr('x', this.anim(time, x3s, x3e - x3s, this.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) {
            [ this.o1, this.o2, this.o3 ].invoke(mode);
            this.resurrect();
        }
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) {
            [ this.o1, this.o2, this.o3 ].invoke(mode);
            this.die();
        }
    }

    resurrect () {
        this.info.forEach(({ image, config }) => {
            image.attr('src', config.src);
        });
    }

    die () {
        this.info.forEach(({ image, config }) => {
            image.attr('src', config.srcF);
        });
    }
}


export default new Lose();
