import tween from '../libs/tween/index.js';
import layer from '../layer.js';
import timeline from '../timeline.js';
import soundFactory from '../factory/sound.js';
import { LAYERS, FLASH_CONFIG, SOUNDS } from '../config-defaults.js';


class Flash {
    image;
    snd;
    xDiff    = 0;
    yDiff    = 0;
    switchOn = true;
    anim     = tween.quadratic.cio;
    anims    = [];
    dur      = FLASH_CONFIG.duration;

    set () {
        if (!this.switchOn) return false;

        this.image = layer.createImage(
            LAYERS.FLASH,
            FLASH_CONFIG.image,
            0,
            0,
            FLASH_CONFIG.width,
            FLASH_CONFIG.height,
        ).hide();

        this.snd = soundFactory.create(SOUNDS.SPLATTER);
    }

    showAt (x, y, an) {
        if (!this.switchOn) return false;

        this.image && this.image.rotate(
            an,
            true,
        ).scale(
            1e-5,
            1e-5,
        ).attr({
            x: x + this.xDiff,
            y: y + this.yDiff,
        }).show();

        this.anims.clear && this.anims.clear();

        this.snd && this.snd.play();

        timeline.createTask({
            start       : 0,
            duration    : this.dur,
            data        : [ 1e-5, 1 ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            recycle     : this.anims,
        });

        timeline.createTask({
            start       : this.dur,
            duration    : this.dur,
            data        : [ 1, 1e-5 ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time, a, b, z) {
        if (!this.switchOn) return false;

        this.image.scale(
            z = this.anim(
                time,
                a,
                b - a,
                this.dur,
            ),
            z,
        );
    }
}


export default new Flash();
