import { DEBUG_MODE, FIRST_SAFE_FRUIT_NUMBER, FRUIT_CONFIG, IS_TOUCH, LAYERS, MESSAGES, MODES } from '../config-defaults.js';
import control from '../control.js';
import layer from '../layer.js';
import { calcSize } from '../layout/calc-size.js';
import viewport from '../layout/viewport.js';
import tween from '../libs/tween/index.js';
import message from '../message.js';
import flash from '../object/flash.js';
import timeline from '../timeline.js';
import { randomNumber } from '../utils.js';
import juiceFactory from './juice.js';


const { height: viewportHeight, width: viewportWidth } = viewport;
const average                    = function (a, b) {
    return ((a + b) / 2) >> 0;
};


class Fruit {
    image;
    shadow;
    zoomAnim              = tween.exponential.co;
    linearAnim            = tween.linear;
    dropAnim              = tween.quadratic.ci;
    fallOffAnim           = tween.quadratic.co;
    rotateSpeed           = [ 60, 50, 40, -40, -50, -60 ];
    brokend               = false;
    apartAngle            = 0;
    aparted               = false;
    shotOutStartX;
    shotOutStartY;
    shotOutEndX;
    shotOutEndY;
    fallOffToX;
    fallTargetX;
    fallTargetY;
    touchedPosX;
    touchedPosY;
    brokenPosX;
    brokenPosY;
    brokenTargetX1;
    brokenTargetY1;
    brokenTargetX2;
    brokenTargetY2;
    touchedTargetX;
    touchedTargetY;
    bImage1RotateAngle;
    bImage2RotateAngle;
    fallOutOfViewerCalled = false;
    fallOffing            = false;
    type;

    constructor (conf) {
        const info   = FRUIT_CONFIG.infos[conf.type];
        const radius = info[3];

        this.type    = conf.type;
        this.originX = conf.originX;
        this.originY = conf.originY;
        this.radius  = radius;
        this.startX  = conf.originX;
        this.startY  = conf.originY;
        this.anims   = [];
    }

    set (hide) {
        const inf    = FRUIT_CONFIG.infos[this.type];
        const radius = this.radius;

        this.shadow = layer.createImage(
            LAYERS.FRUIT,
            FRUIT_CONFIG.shadowImage,
            this.startX - radius,
            this.startY - radius + FRUIT_CONFIG.shadowPos,
            106,
            77,
        );

        this.image = layer.createImage(
            LAYERS.FRUIT,
            inf[0],
            this.startX - radius,
            this.startY - radius,
            inf[1],
            inf[2],
        );

        IS_TOUCH && this.addClickHandler();

        if (this.type === FRUIT_CONFIG.types.BOOM) {
            this.image.rotate(inf[4]);
        }

        if (hide) {
            this.image.hide();
            this.shadow.hide();
        }

        DEBUG_MODE && this.showDebugCatchArea();

        return this;
    }

    clickHandler = (event) => {
        const posX = event.clientX - control.canvasLeft;
        const posY = event.clientY - control.canvasTop;
        message.postMessage([ posX, posY ], MESSAGES.TOUCH);
    };

    addClickHandler () {
        this.image.node.addEventListener('pointerdown', this.clickHandler);
    }

    removeClickHandler () {
        this.image.node.removeEventListener('pointerdown', this.clickHandler);
    }

    showDebugCatchArea () {
        const { originX, originY, radius } = this;

        this.debugArea = this.image.paper.circle(originX, originY, radius).attr({
            stroke            : 'mediumblue',
            'stroke-width'    : 2,
            'stroke-dasharray': '- ',
            opacity           : 0.5,
        });

        this.debugArea.insertBefore(this.image);
    }

    pos (x, y) {
        const r      = this.radius;
        this.originX = x;
        this.originY = y;

        this.image.attr({ x: x -= r, y: y -= r });
        this.shadow.attr({ x: x, y: y + FRUIT_CONFIG.shadowPos });
        DEBUG_MODE && this.debugArea.attr({ cx: x + r, cy: y + r });

        if (this.fallOffing && !this.fallOutOfViewerCalled && y > viewportHeight + this.radius) {
            this.fallOutOfViewerCalled = 1;
            message.postMessage(this, MESSAGES.FRUIT_FALL_OUT_OF_VIEWER);
        }
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1e-5, 1, MODES.SHOW ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeStart : this.onShowStart,
            recycle     : this.anims,
        });
    }

    hide (start) {
        if (this.type !== FRUIT_CONFIG.types.BOOM) return;

        this.anims.clear();

        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1, 1e-5, MODES.HIDE ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeEnd   : this.onHideEnd,
            recycle     : this.anims,
        });
    }

    rotate (start, speed) {
        this.rotateSpeed = speed || this.rotateSpeed[randomNumber(6)];
        timeline.createTask({
            start       : start,
            duration    : -1,
            object      : this,
            onTimeUpdate: this.onRotating,
            recycle     : this.anims,
        });
    }

    broken (angle) {
        if (this.brokend) return;

        this.brokend = true;

        const index = FruitFactory.fruitCache.indexOf(this);
        if (index > -1) FruitFactory.fruitCache.splice(index, 1);

        if (this.type !== FRUIT_CONFIG.types.BOOM) {
            flash.showAt(this.originX, this.originY, angle);
            juiceFactory.create(this.originX, this.originY, FRUIT_CONFIG.infos[this.type][6]);

            IS_TOUCH
                ? this.touched()
                : this.apart(angle);
        } else {
            this.hide();
        }
    }

    pause () {
        if (this.brokend) return;

        this.anims.clear();
    }

    touched () {
        this.anims.clear();
        this.shadow.hide();

        timeline.createTask({
            start       : 0,
            duration    : FRUIT_CONFIG.dropTime,
            data        : [ 1, 1e-5, MODES.HIDE, 1500 ],
            object      : this,
            onTimeUpdate: this.onScaling,
            onTimeStart : this.onTouchedDropStart,
            onTimeEnd   : this.onTouchedDropEnd,
            recycle     : this.anims,
        });

        this.debugArea && this.debugArea.remove();
    }

    apart (angle) {
        this.anims.clear();
        this.image.hide();
        this.shadow.hide();
        this.aparted = true;

        const inf       = FRUIT_CONFIG.infos[this.type];
        const cutImages = FRUIT_CONFIG.cutImages[this.type];
        const radius    = this.radius;
        const create    = layer.createImage.saturate(
            layer,
            this.startX - radius,
            this.startY - radius,
            inf[1],
            inf[2],
        );

        angle = ((angle % 180) + 360 + inf[4]) % 360;

        this.bImage1 = create(LAYERS.FRUIT, cutImages[0]);
        this.bImage2 = create(LAYERS.FRUIT, cutImages[1]);

        [ this.bImage1, this.bImage2 ].invoke('rotate', angle);

        this.apartAngle = angle;

        timeline.createTask({
            start       : 0,
            duration    : FRUIT_CONFIG.dropTime,
            object      : this,
            onTimeUpdate: this.onBrokenDropUpdate,
            onTimeStart : this.onBrokenDropStart,
            onTimeEnd   : this.onBrokenDropEnd,
            recycle     : this.anims,
        });

        this.debugArea && this.debugArea.remove();
    }

    remove () {
        IS_TOUCH && this.removeClickHandler();

        this.anims.clear();

        if (this.image) {
            this.image.remove();
            this.shadow.remove();
            this.debugArea && this.debugArea.remove();
        }

        if (this.bImage1) {
            this.bImage1.remove();
            this.bImage2.remove();
        }

        const index = FruitFactory.fruitCache.indexOf(this);
        if (index > -1) FruitFactory.fruitCache.splice(index, 1);

        for (const name in this) {
            if (typeof this[name] === 'function') {
                this[name] = function (name) {
                    return function () {
                        throw new Error('method ' + name + ' has been removed');
                    };
                }(name);
            } else {
                delete this[name];
            }
        }

        message.postMessage(this, MESSAGES.FRUIT_REMOVE);
    }

    onShowStart () {
        this.image.show();
        this.shadow.show();
    }

    onScaling (time, a, b, z, animationTime = 500) {
        this.image.scale(z = this.zoomAnim(time, a, b - a, animationTime), z);
        this.shadow.scale(z, z);
    }

    onHideEnd () {
        this.remove();
    }

    onRotating (time) {
        this.image.rotate((this.rotateSpeed * time / 1e3) % 360, true);
    }

    onTouchedDropStart () {
        this.touchedTargetX = randomNumber(viewportWidth) - 175;
        this.touchedTargetY = viewportHeight;
        this.touchedPosX    = this.originX;
        this.touchedPosY    = this.originY;

        for (let i = FruitFactory.fruitCache.length - 1; i >= 0; i--) {
            if (FruitFactory.fruitCache[i] === this) {
                FruitFactory.fruitCache.splice(i, 1);
            }
        }
    }

    onTouchedDropEnd () {
        this.remove();
    }

    onBrokenDropUpdate (time) {
        const radius = this.radius;
        this.bImage1.attr({
            x: this.linearAnim(time, this.brokenPosX - radius, this.brokenTargetX1, FRUIT_CONFIG.dropTime),
            y: this.dropAnim(time, this.brokenPosY - radius, this.brokenTargetY1 - this.brokenPosY + radius, FRUIT_CONFIG.dropTime),
        }).rotate(this.linearAnim(time, this.apartAngle, this.bImage1RotateAngle, FRUIT_CONFIG.dropTime), true);
        this.bImage2.attr({
            x: this.linearAnim(time, this.brokenPosX - radius, this.brokenTargetX2, FRUIT_CONFIG.dropTime),
            y: this.dropAnim(time, this.brokenPosY - radius, this.brokenTargetY2 - this.brokenPosY + radius, FRUIT_CONFIG.dropTime),
        }).rotate(this.linearAnim(time, this.apartAngle, this.bImage2RotateAngle, FRUIT_CONFIG.dropTime), true);
    }

    onBrokenDropStart () {
        this.brokenTargetX1     = -(randomNumber(FRUIT_CONFIG.dropXScope) + 75);
        this.brokenTargetX2     = randomNumber(FRUIT_CONFIG.dropXScope + 75);
        this.brokenTargetY1     = viewportHeight;
        this.brokenTargetY2     = viewportHeight;
        this.brokenPosX         = this.originX;
        this.brokenPosY         = this.originY;
        this.bImage1RotateAngle = -randomNumber(150) - 50;
        this.bImage2RotateAngle = randomNumber(150) + 50;

        for (let i = FruitFactory.fruitCache.length - 1; i >= 0; i--) {
            if (FruitFactory.fruitCache[i] === this) {
                FruitFactory.fruitCache.splice(i, 1);
            }
        }
    }

    onBrokenDropEnd () {
        this.remove();
    }

    onShotOuting (time) {
        this.pos(
            this.linearAnim(time, this.shotOutStartX, this.shotOutEndX - this.shotOutStartX, FRUIT_CONFIG.dropTime),
            this.fallOffAnim(time, this.shotOutStartY, this.shotOutEndY - this.shotOutStartY, FRUIT_CONFIG.dropTime),
        );
    }

    onShotOutStart () {
    }

    onShotOutEnd () {
        this.fallOff(0, this.fallOffToX);
    }

    onFalling (time) {
        this.pos(
            this.linearAnim(time, this.brokenPosX, this.fallTargetX - this.brokenPosX, FRUIT_CONFIG.dropTime),
            this.dropAnim(time, this.brokenPosY, this.fallTargetY - this.brokenPosY, FRUIT_CONFIG.dropTime),
        );
    }

    onFallStart () {
        this.brokenPosX = this.originX;
        this.brokenPosY = this.originY;
    }

    onFallEnd () {
        message.postMessage(this, MESSAGES.FRUIT_FALL_OFF);
        this.remove();
    }

    shotOut = (() => {
        const sign = [ -1, 1 ];

        return (start, endX) => {
            this.shotOutStartX = this.originX;
            this.shotOutStartY = this.originY;
            this.shotOutEndX   = average(this.originX, endX);
            this.shotOutEndY   = Math.min(this.startY - randomNumber(this.startY - 100), 200);
            this.fallOffToX    = endX;

            timeline.createTask({
                start       : start,
                duration    : FRUIT_CONFIG.dropTime,
                object      : this,
                onTimeUpdate: this.onShotOuting,
                onTimeStart : this.onShotOutStart,
                onTimeEnd   : this.onShotOutEnd,
                recycle     : this.anims,
            });

            if (this.type !== FRUIT_CONFIG.types.BOOM) {
                this.rotate(0, (randomNumber(180) + 90) * sign[randomNumber(2)]);
            }

            return this;
        };
    })();

    fallOff = (() => {
        const sign    = [ -1, 1 ];
        let signIndex = 0;

        return (start, x) => {
            if (this.aparted || this.brokend) return;

            this.fallOffing = 1;

            const y = viewportHeight + calcSize(120);

            if (typeof x !== 'number') {
                x = this.originX + randomNumber(FRUIT_CONFIG.dropXScope) * sign[(signIndex++) % 2];
            }

            this.fallTargetX = x;
            this.fallTargetY = y;

            timeline.createTask({
                start       : start,
                duration    : FRUIT_CONFIG.dropTime,
                object      : this,
                onTimeUpdate: this.onFalling,
                onTimeStart : this.onFallStart,
                onTimeEnd   : this.onFallEnd,
                recycle     : this.anims,
            });
        }
    })();
}


class FruitFactory {
    static fruitCache = [];
    _safeNumber = FIRST_SAFE_FRUIT_NUMBER;

    types             = [
        FRUIT_CONFIG.types.PEACH,
        FRUIT_CONFIG.types.SANDIA,
        FRUIT_CONFIG.types.APPLE,
        FRUIT_CONFIG.types.BANANA,
        FRUIT_CONFIG.types.BASAHA,
    ];

    create (originX, originY) {
        const fruit = new Fruit({
            type   : this.getType(),
            originX: originX,
            originY: originY,
        }).set();

        FruitFactory.fruitCache.unshift(fruit);

        return fruit;
    }

    getType () {
        const isBoom = randomNumber(8) === 4 && !this.safeNumber;

        this.safeNumber = this.safeNumber - 1;

        if (isBoom) return FRUIT_CONFIG.types.BOOM;
        else return this.types[randomNumber(5)];
    }

    getFruitInView () {
        return FruitFactory.fruitCache;
    }

    get safeNumber () {
        return this._safeNumber;
    }

    set safeNumber (number) {
        this._safeNumber = Math.max(number, 0);
    }
}


export default new FruitFactory();
