import layer from '../layer.js';
import { CONSOLE_CONFIG } from '../config-defaults.js';


class Console {
    x     = CONSOLE_CONFIG.x;
    y     = CONSOLE_CONFIG.y;
    texts = [];

    set () {
    }

    clear () {
        const length = this.texts.length;

        for (let i = 0; i < length; i++) {
            this.texts[i].remove();
        }

        this.texts.length = 0;
        this.y            = CONSOLE_CONFIG.y;
    }

    log (text) {
        this.y += 20;
        this.texts.push(layer.createText(
            'default',
            text,
            this.x,
            this.y,
        ));
    }
}


export default new Console();
