import fruitFactory from './factory/fruit.js';
import soundFactory from './factory/sound.js';

import timeline from './timeline.js';
import state from './state.js';
import message from './message.js';

import light from './object/light.js';
import score from './object/score.js';
import background from './object/background.js';
import knifes from './object/knifes.js';
import lose from './object/lose.js';
import gameOver from './object/game-over.js';
import vibration from './object/vibration.js';
import { randomNumber } from './utils.js';
import { FRUIT_CONFIG, MESSAGES, STATE_KEYS, GAME_STATES, SOUNDS, SCORE_MULTIPLIER } from './config-defaults.js';
import viewport from './layout/viewport.js';


const { width: viewportWidth, height: viewportHeight } = viewport;


class Game {
    scoreNumber          = 0;
    volleyNum            = 2;
    volleyMultipleNumber = 5;
    fruits               = [];
    gameInterval;
    snd;
    boomSnd;

    constructor () {
        message.addEventListener(MESSAGES.FRUIT_REMOVE, (fruit) => {
            const index = this.fruits.indexOf(fruit);

            if (index > -1) {
                this.fruits.splice(index, 1);
            }
        });

        message.addEventListener(MESSAGES.FRUIT_FALL_OUT_OF_VIEWER, (fruit) => {
            if (fruit.type === FRUIT_CONFIG.types.BOOM) return;
            lose.showLoseAt();
        });

        message.addEventListener(MESSAGES.GAME_OVER, () => {
            this.gameOver();
            knifes.switchOn();
        });

        message.addEventListener(MESSAGES.OVER_WHITE_LIGHT_SHOW, () => {
            knifes.endAll();

            for (let i = this.fruits.length - 1; i >= 0; i--) {
                this.fruits[i].remove();
            }

            background.stop();
        });

        message.addEventListener(MESSAGES.CLICK, () => {
            state(STATE_KEYS.CLICK_ENABLE).off();
            gameOver.hide();
            message.postMessage(MESSAGES.HOME_MENU, MESSAGES.SCENE_SWITCH_SCENE);
        });
    }

    barbette = () => {
        if (this.fruits.length >= this.volleyNum) {
            return;
        }

        const startX = randomNumber(viewportWidth);
        const endX   = randomNumber(viewportWidth);
        const startY = viewportHeight;
        const f      = fruitFactory.create(startX, startY).shotOut(0, endX);

        this.fruits.push(f);
        this.snd.play();

        this.barbette();
    }

    start () {
        lose.number = 0;

        this.scoreNumber = 0;
        this.volleyNum   = 2;

        this.snd     = soundFactory.create(SOUNDS.THROW);
        this.boomSnd = soundFactory.create(SOUNDS.BOOM);

        timeline.setTimeout(() => {
            state(STATE_KEYS.GAME_STATE).set(GAME_STATES.PLAYING);
            this.gameInterval = timeline._setInterval(this.barbette, 1e3);
        }, 500);
    }

    gameOver () {
        state(STATE_KEYS.GAME_STATE).set(GAME_STATES.OVER);
        this.gameInterval.stop();
        this.fruits.length = 0;
    }

    applyScore (score) {
        if (score > this.volleyNum * this.volleyMultipleNumber) {
            this.volleyNum++;
        }
        this.volleyMultipleNumber += 50;
    }

    sliceAt (fruit, angle) {
        const index = this.fruits.indexOf(fruit);

        if (state(STATE_KEYS.GAME_STATE).isnot(GAME_STATES.PLAYING)) return;

        if (fruit.type !== FRUIT_CONFIG.types.BOOM) {
            fruit.broken(angle);
            vibration.fruit();
            if (index) this.fruits.splice(index, 1);
            this.scoreNumber += (SCORE_MULTIPLIER || 0);
            score.number(this.scoreNumber);
            this.applyScore(this.scoreNumber);
        } else {
            lose.die();
            this.pauseAllFruit();
            this.boomSnd.play();
            background.wobble();
            vibration.boom();
            light.start(fruit, background.stop);
        }
    }

    catchFruit (fruit) {
        const index = this.fruits.indexOf(fruit);

        if (state(STATE_KEYS.GAME_STATE).isnot(GAME_STATES.PLAYING)) return;

        if (fruit.type !== FRUIT_CONFIG.types.BOOM) {
            fruit.broken(0);
            vibration.fruit();
            if (index !== -1) this.fruits.splice(index, 1);
            score.number(++this.scoreNumber);
            this.applyScore(this.scoreNumber);
        } else {
            lose.die();
            this.pauseAllFruit();
            this.boomSnd.play();
            background.wobble();
            vibration.boom();
            light.start(fruit, background.stop);
        }
    }

    pauseAllFruit () {
        this.gameInterval.stop();
        knifes.pause();
        this.fruits.invoke('pause');
    }
}


export default new Game();
