function unsetObject (object) {
    for (const i in object) {
        if (object.hasOwnProperty(i) && typeof object[i] == 'function') {
            object[i] = function () {};
        }
    }
}

function getAngleByRadian (radian) {
    return radian * 180 / Math.PI;
}

function pointToRadian (origin, point) {
    if (point[0] === origin[0]) {
        if (point[1] > origin[1]) return Math.PI * 0.5;
        return Math.PI * 1.5
    } else if (point[1] === origin[1]) {
        if (point[0] > origin[0]) return 0;
        return Math.PI;
    }

    const t = Math.atan((origin[1] - point[1]) / (origin[0] - point[0]));

    if (point[0] > origin[0] && point[1] < origin[1]) return t + 2 * Math.PI;

    if (point[0] > origin[0] && point[1] > origin[1]) return t;

    return t + Math.PI;
}

export default {
    unsetObject,
    getAngleByRadian,
    pointToRadian,
};
