import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import timeline from '../timeline.js';
import message from '../message.js';
import { HOME_TEXT_CONFIG, LAYERS, START_BUTTON_CONFIG, MODES, MESSAGES } from '../config-defaults.js';
import vibration from './vibration.js';


class HomeButton {
    text;
    button;
    group;
    anims = [];
    anim  = tween.exponential.co;

    set () {
        this.button = layer.createImage(
            LAYERS.START_SCREEN,
            START_BUTTON_CONFIG.image,
            START_BUTTON_CONFIG.startX,
            START_BUTTON_CONFIG.startY,
            START_BUTTON_CONFIG.width,
            START_BUTTON_CONFIG.height,
        ).hide();

        this.text = layer.createText(
            LAYERS.START_SCREEN,
            START_BUTTON_CONFIG.textValue,
            START_BUTTON_CONFIG.textStartX,
            START_BUTTON_CONFIG.textStartY,
            START_BUTTON_CONFIG.textFill,
            START_BUTTON_CONFIG.textSize,
            START_BUTTON_CONFIG.textWeight,
            START_BUTTON_CONFIG.textAnchor,
        );

        this.group = layer.getLayer(LAYERS.START_SCREEN).set();
        this.group.push(this.button, this.text);
        layer.hideLayer(LAYERS.START_SCREEN);

        this.group.click(() => {
            message.postMessage(MESSAGES.START_GAME);
            vibration.vibrate();
        });
    }

    show (start) {
        timeline.createTask({
            start,
            duration: START_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                START_BUTTON_CONFIG.startY,
                START_BUTTON_CONFIG.endY,
                START_BUTTON_CONFIG.textStartY,
                START_BUTTON_CONFIG.textEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration: START_BUTTON_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                START_BUTTON_CONFIG.endY,
                START_BUTTON_CONFIG.startY,
                START_BUTTON_CONFIG.textEndY,
                START_BUTTON_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    onTimeUpdate (time, mode, bsy, bey, tsy, tey) {
        this.button.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.text.attr('y', this.anim(time, tsy, tey - tsy, HOME_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) [ this.button ].invoke(mode);
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) [ this.button ].invoke(mode);
    }
}


export default new HomeButton();
