import { LAYERS } from '../config-defaults.js';
import displacementFactory from './displacement.js';


class Preload {
    constructor (imageSrc) {
        displacementFactory.create({
            layer: LAYERS.PRELOAD,
            imageSrc,
        }).set();
    }
}


class PreloadFactory {
    create (imageSrc) {
        return new Preload(imageSrc);
    }
}


export default new PreloadFactory();
