import { HOME_TEXT_CONFIG, LAYERS, MODES } from '../config-defaults.js';
import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import timeline from '../timeline.js';


class HomeText {
    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    text1;
    text2;
    text3;

    set () {
        this.text1 = layer.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text1Value,
            HOME_TEXT_CONFIG.text1Sx,
            HOME_TEXT_CONFIG.text1Y,
            HOME_TEXT_CONFIG.text1Fill,
            HOME_TEXT_CONFIG.text1Size,
            HOME_TEXT_CONFIG.text1Weight,
            HOME_TEXT_CONFIG.text1Anchor,
        ).hide();

        this.text2 = layer.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text2Value,
            HOME_TEXT_CONFIG.text2Sx,
            HOME_TEXT_CONFIG.text2Y,
            HOME_TEXT_CONFIG.text2Fill,
            HOME_TEXT_CONFIG.text2Size,
            HOME_TEXT_CONFIG.text2Weight,
            HOME_TEXT_CONFIG.text2Anchor,
        ).hide();

        this.text3 = layer.createText(
            LAYERS.START_SCREEN,
            HOME_TEXT_CONFIG.text3Value,
            HOME_TEXT_CONFIG.text3Sx,
            HOME_TEXT_CONFIG.text3Y,
            HOME_TEXT_CONFIG.text3Fill,
            HOME_TEXT_CONFIG.text3Size,
            HOME_TEXT_CONFIG.text3Weight,
            HOME_TEXT_CONFIG.text3Anchor,
        ).hide();
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : HOME_TEXT_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                HOME_TEXT_CONFIG.text1Sx,
                HOME_TEXT_CONFIG.text1Ex,
                HOME_TEXT_CONFIG.text2Sx,
                HOME_TEXT_CONFIG.text2Ex,
                HOME_TEXT_CONFIG.text3Sx,
                HOME_TEXT_CONFIG.text3Ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : HOME_TEXT_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                HOME_TEXT_CONFIG.text1Ex,
                HOME_TEXT_CONFIG.text1Sx,
                HOME_TEXT_CONFIG.text2Ex,
                HOME_TEXT_CONFIG.text2Sx,
                HOME_TEXT_CONFIG.text3Ex,
                HOME_TEXT_CONFIG.text3Sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time, mode, t1sx, t1ex, t2sx, t2ex, t3sx, t3ex) {
        this.text1.attr('x', this.anim(time, t1sx, t1ex - t1sx, HOME_TEXT_CONFIG.animLength));
        this.text2.attr('x', this.anim(time, t2sx, t2ex - t2sx, HOME_TEXT_CONFIG.animLength));
        this.text3.attr('x', this.anim(time, t3sx, t3ex - t3sx, HOME_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) [ this.text1, this.text2, this.text3 ].invoke(mode);
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) [ this.text1, this.text2, this.text3 ].invoke(mode);
    }
}

export default new HomeText();
