import viewport from './viewport.js';


const HORIZONTAL_BASE = 393;
const VERTICAL_BASE = 852;
const BASE_RESULT_CHARS_NUMBER = 6;
const { width, height } = viewport;


export const calcSize = (size) => {
    const ratio = width / HORIZONTAL_BASE;
    return size * ratio;
};

export const calcVerticalSize = (size) => {
    const ratio = height / VERTICAL_BASE;
    return size * ratio;
};

export const calcResultFontSize = (string, base) => {
    const fontSize = calcSize(base);
    const stringLength = string.length;
    const charsNumber = stringLength > BASE_RESULT_CHARS_NUMBER ? stringLength : BASE_RESULT_CHARS_NUMBER;
    return fontSize * BASE_RESULT_CHARS_NUMBER / charsNumber;
};
