import layer from '../layer.js';
import timeline from '../timeline.js';
import { randomNumber } from '../utils.js';
import { BACKGROUND_CONFIG, LAYERS } from '../config-defaults.js';


class Background {
    image;
    time;

    set () {
        this.image = layer.createImage(
            LAYERS.DEFAULT,
            BACKGROUND_CONFIG.image,
            BACKGROUND_CONFIG.x,
            BACKGROUND_CONFIG.y,
            BACKGROUND_CONFIG.width,
            BACKGROUND_CONFIG.height,
            BACKGROUND_CONFIG.preserveAspectRatio,
        );
    }

    getWobble = () => {
        const x = randomNumber(12) - 6;
        const y = randomNumber(12) - 6;
        this.image.attr({ x: x, y: y });
    };

    wobble () {
        this.time = timeline._setInterval(this.getWobble, 50);
    }

    stop = () => {
        this.time.stop();
        this.image.attr({ x: 0, y: 0 });
    }
}


export default new Background();
