import tween from '../libs/tween/index.js';
import timeline from '../timeline.js';
import layer from '../layer.js';
import { LAYERS, MODES, SCORE_CONFIG } from '../config-defaults.js';


class Score {
    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    image;
    text1;
    text2;

    set () {
        this.image = layer.createImage(
            LAYERS.DEFAULT,
            SCORE_CONFIG.image,
            SCORE_CONFIG.imageSx,
            SCORE_CONFIG.imageY,
            SCORE_CONFIG.imageW,
            SCORE_CONFIG.imageH,
        ).hide();

        this.text1 = layer.createText(
            LAYERS.DEFAULT,
            SCORE_CONFIG.text1Value,
            SCORE_CONFIG.text1Sx,
            SCORE_CONFIG.text1Y,
            SCORE_CONFIG.text1Fill,
            SCORE_CONFIG.text1Size,
            SCORE_CONFIG.text1Weight,
        ).hide();

        this.text2 = layer.createText(
            LAYERS.DEFAULT,
            SCORE_CONFIG.text2Prefix + SCORE_CONFIG.text2Value,
            SCORE_CONFIG.text2Sx,
            SCORE_CONFIG.text2Y,
            SCORE_CONFIG.text2Fill,
            SCORE_CONFIG.text2Size,
            SCORE_CONFIG.text2Weight,
        ).hide();
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : SCORE_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                SCORE_CONFIG.imageSx,
                SCORE_CONFIG.imageEx,
                SCORE_CONFIG.text1Sx,
                SCORE_CONFIG.text1Ex,
                SCORE_CONFIG.text2Sx,
                SCORE_CONFIG.text2Ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : SCORE_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                SCORE_CONFIG.imageEx,
                SCORE_CONFIG.imageSx,
                SCORE_CONFIG.text1Ex,
                SCORE_CONFIG.text1Sx,
                SCORE_CONFIG.text2Ex,
                SCORE_CONFIG.text2Sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    number (number) {
        this.text1.attr('text', number || 0);
        this.image.scale(1.2, 1.2);
        this.setTimeout(() => {
            this.image.scale(1, 1);
        }, 60);
    }

    onTimeUpdate (time, mode, isx, iex, t1sx, t1ex, t2sx, t2ex) {
        this.image.attr('x', this.anim(time, isx, iex - isx, SCORE_CONFIG.animLength));
        this.text1.attr('x', this.anim(time, t1sx, t1ex - t1sx, SCORE_CONFIG.animLength));
        this.text2.attr('x', this.anim(time, t2sx, t2ex - t2sx, SCORE_CONFIG.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) [ this.image, this.text1, this.text2 ].invoke(mode);
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) {
            [ this.image, this.text1, this.text2 ].invoke(mode)
            this.text1.attr('text', 0);
        }
    }
}


export default new Score();
