import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import message from '../message.js';
import timeline from '../timeline.js';
import { HOME_TEXT_CONFIG, LAYERS, RESULT_BUTTON_NEXT_CONFIG, MODES, SCENES, MESSAGES } from '../config-defaults.js';
import vibration from './vibration.js';


class ResultButtonNext {
    text;
    button;
    group;
    anims = [];
    anim  = tween.exponential.co;

    set () {
        this.button = layer.createImage(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_NEXT_CONFIG.image,
            RESULT_BUTTON_NEXT_CONFIG.startX,
            RESULT_BUTTON_NEXT_CONFIG.startY,
            RESULT_BUTTON_NEXT_CONFIG.width,
            RESULT_BUTTON_NEXT_CONFIG.height,
        ).hide();

        this.text = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_BUTTON_NEXT_CONFIG.textValue,
            RESULT_BUTTON_NEXT_CONFIG.textStartX,
            RESULT_BUTTON_NEXT_CONFIG.textStartY,
            RESULT_BUTTON_NEXT_CONFIG.textFill,
            RESULT_BUTTON_NEXT_CONFIG.textSize,
            RESULT_BUTTON_NEXT_CONFIG.textWeight,
            RESULT_BUTTON_NEXT_CONFIG.textAnchor,
        );

        this.group = layer.getLayer(LAYERS.RESULT_SCREEN).set();
        this.group.push(this.button, this.text);
        layer.hideLayer(LAYERS.RESULT_SCREEN);

        this.group.click(() => {
            vibration.vibrate();
            message.postMessage(SCENES.BRAND_INTRO, MESSAGES.SCENE_SWITCH_SCENE);
        });
    }

    show (start) {
        timeline.createTask({
            start,
            duration: RESULT_BUTTON_NEXT_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                RESULT_BUTTON_NEXT_CONFIG.startY,
                RESULT_BUTTON_NEXT_CONFIG.endY,
                RESULT_BUTTON_NEXT_CONFIG.textStartY,
                RESULT_BUTTON_NEXT_CONFIG.textEndY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start   : start,
            duration: RESULT_BUTTON_NEXT_CONFIG.animLength,
            data    : [
                MODES.SHOW,
                RESULT_BUTTON_NEXT_CONFIG.endY,
                RESULT_BUTTON_NEXT_CONFIG.startY,
                RESULT_BUTTON_NEXT_CONFIG.textEndY,
                RESULT_BUTTON_NEXT_CONFIG.textStartY,
            ],
            object  : this,
            onTime  : this.onTimeUpdate,
            recycle : this.anims,
        });
    }

    onTimeUpdate (time, mode, bsy, bey, tsy, tey) {
        this.button.attr('y', this.anim(time, bsy, bey - bsy, HOME_TEXT_CONFIG.animLength));
        this.text.attr('y', this.anim(time, tsy, tey - tsy, HOME_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) [ this.button ].invoke(mode);
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) [ this.button ].invoke(mode);
    }
}


export default new ResultButtonNext();
