import layer from '../layer.js';
import message from '../message.js';
import timeline from '../timeline.js';
import { randomNumber } from '../utils.js';
import { LAYERS, MESSAGES, LIGHTS_CONFIG, SCENES } from '../config-defaults.js';
import viewport from '../layout/viewport.js';


const { width: viewportWidth, height: viewportHeight } = viewport;


class Light {
    maskLayer  = layer.getLayer(LAYERS.MASK);
    lightLayer = layer.getLayer(LAYERS.LIGHT);
    lights     = [];
    indexes    = [];
    lightsNum  = LIGHTS_CONFIG.number;

    constructor () {
        for (let i = 0; i < this.lightsNum; i++) {
            this.indexes[i] = i;
        }

        layer.hideLayer(LAYERS.LIGHT);
        layer.hideLayer(LAYERS.MASK);
    }

    start (boom, callback) {
        const x   = boom.originX;
        const y   = boom.originY;
        const idx = this.indexes.random();
        let time  = 0;

        const b = (i) => {
            this.build(x, y, idx[i]);
        };

        for (let i = 0; i < this.lightsNum; i++) {
            timeline.setTimeout(() => b(i), time += LIGHTS_CONFIG.appearanceTime);
        }

        timeline.setTimeout(() => {
            this.overWhiteLight();
            callback && callback();
        }, time);
    }

    overWhiteLight = () => {
        message.postMessage(MESSAGES.OVER_WHITE_LIGHT_SHOW);
        this.removeLights();

        const dur     = LIGHTS_CONFIG.maskAppearanceTime;
        const mask    = this.maskLayer.rect(0, 0, viewportWidth, viewportHeight).attr({
            fill  : LIGHTS_CONFIG.fill,
            stroke: 'none',
        });
        const control = {
            onTimeUpdate: function (time) {
                mask.attr('opacity', 1 - time / dur);
            },

            onTimeEnd: function () {
                mask.remove();
                message.postMessage(MESSAGES.GAME_OVER);
                message.postMessage(SCENES.GAME_OVER, MESSAGES.SCENE_SWITCH_SCENE);
                layer.hideLayer(LAYERS.LIGHT);
                layer.hideLayer(LAYERS.MASK);
            },
        };

        layer.showLayer(LAYERS.MASK);

        timeline.createTask({
            start       : 0,
            duration    : dur,
            object      : control,
            onTimeUpdate: control.onTimeUpdate,
            onTimeEnd   : control.onTimeEnd,
        });
    }

    removeLights () {
        const l = this.lights.length;
        for (let i = 0; i < l; i++) {
            this.lights[i].remove();
        }
        this.lights.length = 0;
    }

    build (x, y, r) {
        let a1 = r * 36 + randomNumber(10);
        let a2 = a1 + 5;

        a1 = Math.PI * a1 / 180;
        a2 = Math.PI * a2 / 180;

        const x1    = x + (2 * viewportWidth) * Math.cos(a1);
        const y1    = y + (2 * viewportWidth) * Math.sin(a1);
        const x2    = x + (2 * viewportWidth) * Math.cos(a2);
        const y2    = y + (2 * viewportWidth) * Math.sin(a2);
        const light = this.lightLayer.path([ 'M', x, y, 'L', x1, y1, 'L', x2, y2, 'Z' ]).attr({
            stroke: 'none',
            fill  : LIGHTS_CONFIG.fill,
        });

        layer.showLayer(LAYERS.LIGHT);

        this.lights.push(light);
    }
}


export default new Light();
