import Ucren from './libs/ucren.js';
import message from './message.js';
import knifes from './object/knifes.js';
import { MESSAGES, INTERACTION_MODE, INTERACTION_MODES } from './config-defaults.js';
import viewport from './layout/viewport.js';


const { width: viewportWidth, height: viewportHeight } = viewport;


class Control {
    canvasTop  = 0;
    canvasLeft = 0;

    init () {
        this.fixCanvasPos();
        this.installDragger();
    }

    fix = () => {
        this.canvasLeft = (document.documentElement.clientWidth - viewportWidth) / 2;
        this.canvasTop  = (document.documentElement.clientHeight - viewportHeight) / 2;
    };

    fixCanvasPos () {
        this.fix();
        Ucren.addEvent(window, 'resize', this.fix);
    }

    dragHandler = (dx, dy, x, y, kf) => {
        const posX = x - this.canvasLeft;
        const posY = y - this.canvasTop;

        if (INTERACTION_MODE === INTERACTION_MODES.SLICE) {
            const throughResult = knifes.through(posX, posY);
            if (throughResult) message.postMessage(throughResult, MESSAGES.SLICE);
        } else if (INTERACTION_MODE === INTERACTION_MODES.TOUCH) {
            message.postMessage([ posX, posY ], MESSAGES.TOUCH);
        }
    };

    startDragHandler = () => {
        knifes.newKnife();
    };

    installDragger () {
        const dragger = new Ucren.BasicDrag({ type: 'calc' });

        dragger.on('returnValue', this.dragHandler);

        dragger.on('startDrag', this.startDragHandler);

        dragger.bind(document.documentElement);
    }
}


export default new Control();
