import fruitFactory from './factory/fruit.js';


class Collide {
    checkSlice (knifePath) {
        const collidedFruits = [];
        let index            = 0;

        fruitFactory.getFruitInView().forEach(fruit => {
            const isColliding = this.isLineIntersectingEllipse(
                knifePath.slice(0, 2),
                knifePath.slice(2, 4),
                [ fruit.originX, fruit.originY ],
                fruit.radius,
            );
            if (isColliding) collidedFruits[index++] = fruit;
        });
        return collidedFruits;
    }

    checkTouch (position) {
        const collidedFruits = [];
        let index            = 0;

        fruitFactory.getFruitInView().forEach(fruit => {
            const isColliding = this.isPointInsideEllipse(
                position,
                [ fruit.originX, fruit.originY ],
                fruit.radius,
            );
            if (isColliding) collidedFruits[index++] = fruit;
        });
        return collidedFruits;
    }

    isPointInsideEllipse(point, center, radius) {
        const dx = point[0] - center[0];
        const dy = point[1] - center[1];
        return (dx * dx + dy * dy) <= (radius * radius);
    }

    isLineIntersectingEllipse (startPoint, endPoint, ellipseCenter, radius, eccentricity = 1) {
        const intersectionPoints = this.calculateLineEllipseIntersections(
            startPoint,
            endPoint,
            ellipseCenter,
            radius,
            eccentricity,
        );
        return intersectionPoints && (intersectionPoints[0] || intersectionPoints[1]);
    }

    calculateLineEllipseIntersections (startPoint, endPoint, ellipseCenter, radius, eccentricity = 1) {
        if (radius <= 0) return;

        const semiMajorAxis = radius;
        const semiMinorAxis = radius * eccentricity;

        const quadraticA =
            this.square(semiMinorAxis) * this.square(startPoint[0] - endPoint[0]) +
            this.square(semiMajorAxis) * this.square(startPoint[1] - endPoint[1]);

        if (quadraticA <= 0) return;

        const quadraticB =
            2 * this.square(semiMinorAxis) * (endPoint[0] - startPoint[0]) * (startPoint[0] - ellipseCenter[0]) +
            2 * this.square(semiMajorAxis) * (endPoint[1] - startPoint[1]) * (startPoint[1] - ellipseCenter[1]);

        const quadraticC =
              this.square(semiMinorAxis) * this.square(startPoint[0] - ellipseCenter[0]) +
              this.square(semiMajorAxis) * this.square(startPoint[1] - ellipseCenter[1]) -
              this.square(semiMajorAxis) * this.square(semiMinorAxis);

        const roots = this.solveQuadraticEquation(quadraticA, quadraticB, quadraticC);

        if (!roots) return;

        const intersectionPoints = [
            [
                startPoint[0] + roots[0] * (endPoint[0] - startPoint[0]),
                startPoint[1] + roots[0] * (endPoint[1] - startPoint[1]),
            ],
            [
                startPoint[0] + roots[1] * (endPoint[0] - startPoint[0]),
                startPoint[1] + roots[1] * (endPoint[1] - startPoint[1]),
            ],
        ];

        if (!this.isPointOnSegment(intersectionPoints[0], startPoint, endPoint)) {
            intersectionPoints[0] = null;
        }

        if (!this.isPointOnSegment(intersectionPoints[1], startPoint, endPoint)) {
            intersectionPoints[1] = null;
        }

        return intersectionPoints;
    }

    isPointOnSegment (point, segmentStart, segmentEnd) {
        if (!point) return false;

        return (
            this.getSign(point[0] - segmentStart[0]) * this.getSign(point[0] - segmentEnd[0]) <= 0 &&
            this.getSign(point[1] - segmentStart[1]) * this.getSign(point[1] - segmentEnd[1]) <= 0
        );
    }

    square (value) {
        return Math.pow(value, 2);
    }

    getSign (value) {
        return value < 0 ? -1 : value > 0 ? 1 : 0;
    }

    solveQuadraticEquation (a, b, c) {
        if (a === 0) return;

        const discriminant = b * b - 4 * a * c;
        if (discriminant === 0) {
            const root = -b / (2 * a);
            return [ root, root ];
        } else if (discriminant > 0) {
            const sqrtDiscriminant = Math.sqrt(discriminant);
            return [
                (-b + sqrtDiscriminant) / (2 * a),
                (-b - sqrtDiscriminant) / (2 * a),
            ];
        }
    }
}


export default new Collide();
