import tween from '../libs/tween/index.js';
import layer from '../layer.js';
import state from '../state.js';
import timeline from '../timeline.js';
import { LAYERS, GAME_OVER_CONFIG, MODES, STATE_KEYS } from '../config-defaults.js';


class GameOver {
    image;
    anims = [];

    set () {
        this.image = layer.createImage(
            LAYERS.DEFAULT,
            GAME_OVER_CONFIG.image,
            GAME_OVER_CONFIG.x,
            GAME_OVER_CONFIG.y,
            GAME_OVER_CONFIG.width,
            GAME_OVER_CONFIG.height,
        ).hide().scale(
            1e-5,
            1e-5,
        );
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1e-5, 1, MODES.SHOW ],
            object      : this,
            onTimeUpdate: this.onZooming,
            onTimeStart : this.onZoomStart,
            onTimeEnd   : this.onZoomEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : 500,
            data        : [ 1, 1e-5, MODES.HIDE ],
            object      : this,
            onTimeUpdate: this.onZooming,
            onTimeStart : this.onZoomStart,
            onTimeEnd   : this.onZoomEnd,
            recycle     : this.anims,
        });
    }

    onZoomStart (sz, ez, mode) {
        if (mode === MODES.SHOW) this.image.show();
    }

    onZooming (time, sz, ez, z) {
        this.image.scale(
            z = tween.exponential.co(
                time,
                sz,
                ez - sz,
                500,
            ),
            z,
        );
    }

    onZoomEnd (sz, ez, mode) {
        if (mode === MODES.SHOW) {
            state(STATE_KEYS.CLICK_ENABLE).on();
        } else if (mode === MODES.HIDE) {
            this.image.hide();
        }
    }
}


export default new GameOver();
