import { RESULT_TEXT_CONFIG, LAYERS, MODES, MESSAGES } from '../config-defaults.js';
import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import message from '../message.js';
import timeline from '../timeline.js';
import game from '../game.js';


class ResultText {
    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    text1;
    text2;
    text3;
    text4;
    text5;
    text6;
    text7;

    constructor () {
        message.addEventListener(MESSAGES.GAME_OVER, () => {
            this.text3.attr('text', game.scoreNumber);
        });
    }

    set () {
        this.text1 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text1Value,
            RESULT_TEXT_CONFIG.text1X,
            RESULT_TEXT_CONFIG.text1Sy,
            RESULT_TEXT_CONFIG.text1Fill,
            RESULT_TEXT_CONFIG.text1Size,
            RESULT_TEXT_CONFIG.text1Weight,
            RESULT_TEXT_CONFIG.text1Anchor,
        ).hide();

        this.text2 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text2Value,
            RESULT_TEXT_CONFIG.text2X,
            RESULT_TEXT_CONFIG.text2Sy,
            RESULT_TEXT_CONFIG.text2Fill,
            RESULT_TEXT_CONFIG.text2Size,
            RESULT_TEXT_CONFIG.text2Weight,
            RESULT_TEXT_CONFIG.text2Anchor,
        ).hide();

        this.text3 = layer.createText(
            LAYERS.RESULT_SCREEN,
            game.scoreNumber,
            RESULT_TEXT_CONFIG.text3X,
            RESULT_TEXT_CONFIG.text3Sy,
            RESULT_TEXT_CONFIG.text3Fill,
            RESULT_TEXT_CONFIG.text3Size,
            RESULT_TEXT_CONFIG.text3Weight,
            RESULT_TEXT_CONFIG.text3Anchor,
        )
            .attr('stroke', 'white')
            .attr('stroke-width', 3)
            .hide();


        this.text4 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text4Value,
            RESULT_TEXT_CONFIG.text4Sx,
            RESULT_TEXT_CONFIG.text4Y,
            RESULT_TEXT_CONFIG.text4Fill,
            RESULT_TEXT_CONFIG.text4Size,
            RESULT_TEXT_CONFIG.text4Weight,
            RESULT_TEXT_CONFIG.text4Anchor,
        ).hide();

        this.text5 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text5Value,
            RESULT_TEXT_CONFIG.text5Sx,
            RESULT_TEXT_CONFIG.text5Y,
            RESULT_TEXT_CONFIG.text5Fill,
            RESULT_TEXT_CONFIG.text5Size,
            RESULT_TEXT_CONFIG.text5Weight,
            RESULT_TEXT_CONFIG.text5Anchor,
        )
            .attr('stroke', 'white')
            .attr('stroke-width', 2)
            .hide();

        this.text6 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text6Value,
            RESULT_TEXT_CONFIG.text6Sx,
            RESULT_TEXT_CONFIG.text6Y,
            RESULT_TEXT_CONFIG.text6Fill,
            RESULT_TEXT_CONFIG.text6Size,
            RESULT_TEXT_CONFIG.text6Weight,
            RESULT_TEXT_CONFIG.text6Anchor,
        ).hide();

        this.text7 = layer.createText(
            LAYERS.RESULT_SCREEN,
            RESULT_TEXT_CONFIG.text7Value,
            RESULT_TEXT_CONFIG.text7Sx,
            RESULT_TEXT_CONFIG.text7Y,
            RESULT_TEXT_CONFIG.text7Fill,
            RESULT_TEXT_CONFIG.text7Size,
            RESULT_TEXT_CONFIG.text7Weight,
            RESULT_TEXT_CONFIG.text7Anchor,
        )
            .attr('stroke', 'white')
            .attr('stroke-width', 2)
            .hide();
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : RESULT_TEXT_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                RESULT_TEXT_CONFIG.text1Sy,
                RESULT_TEXT_CONFIG.text1Ey,
                RESULT_TEXT_CONFIG.text2Sy,
                RESULT_TEXT_CONFIG.text2Ey,
                RESULT_TEXT_CONFIG.text3Sy,
                RESULT_TEXT_CONFIG.text3Ey,
                RESULT_TEXT_CONFIG.text4Sx,
                RESULT_TEXT_CONFIG.text4Ex,
                RESULT_TEXT_CONFIG.text5Sx,
                RESULT_TEXT_CONFIG.text5Ex,
                RESULT_TEXT_CONFIG.text6Sx,
                RESULT_TEXT_CONFIG.text6Ex,
                RESULT_TEXT_CONFIG.text7Sx,
                RESULT_TEXT_CONFIG.text7Ex,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : RESULT_TEXT_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                RESULT_TEXT_CONFIG.text1Ey,
                RESULT_TEXT_CONFIG.text1Sy,
                RESULT_TEXT_CONFIG.text2Ey,
                RESULT_TEXT_CONFIG.text2Sy,
                RESULT_TEXT_CONFIG.text3Ey,
                RESULT_TEXT_CONFIG.text3Sy,
                RESULT_TEXT_CONFIG.text4Ex,
                RESULT_TEXT_CONFIG.text4Sx,
                RESULT_TEXT_CONFIG.text5Ex,
                RESULT_TEXT_CONFIG.text5Sx,
                RESULT_TEXT_CONFIG.text6Ex,
                RESULT_TEXT_CONFIG.text6Sx,
                RESULT_TEXT_CONFIG.text7Ex,
                RESULT_TEXT_CONFIG.text7Sx,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time, mode, t1sy, t1ey, t2sy, t2ey, t3sy, t3ey, t4sx, t4ex, t5sx, t5ex, t6sx, t6ex, t7sx, t7ex) {
        this.text1.attr('y', this.anim(time, t1sy, t1ey - t1sy, RESULT_TEXT_CONFIG.animLength));
        this.text2.attr('y', this.anim(time, t2sy, t2ey - t2sy, RESULT_TEXT_CONFIG.animLength));
        this.text3.attr('y', this.anim(time, t3sy, t3ey - t3sy, RESULT_TEXT_CONFIG.animLength));
        this.text4.attr('x', this.anim(time, t4sx, t4ex - t4sx, RESULT_TEXT_CONFIG.animLength));
        this.text5.attr('x', this.anim(time, t5sx, t5ex - t5sx, RESULT_TEXT_CONFIG.animLength));
        this.text6.attr('x', this.anim(time, t6sx, t6ex - t6sx, RESULT_TEXT_CONFIG.animLength));
        this.text7.attr('x', this.anim(time, t7sx, t7ex - t7sx, RESULT_TEXT_CONFIG.animLength));
    }

    onTimeStart (mode) {
        if (mode === MODES.SHOW) {
            [ this.text1, this.text2, this.text3, this.text4, this.text5, this.text6, this.text7 ].invoke(mode);
        }
    }

    onTimeEnd (mode) {
        if (mode === MODES.HIDE) {
            [ this.text1, this.text2, this.text3, this.text4, this.text5, this.text6, this.text7 ].invoke(mode);
        }
    }
}

export default new ResultText();
