import layer from '../layer.js';
import timeline from '../timeline.js';
import { DEBUG_MODE, FPS_CONFIG, LAYERS } from '../config-defaults.js';


class FPS {
    text;
    prefix = FPS_CONFIG.prefix;
    fill   = FPS_CONFIG.fill;

    set () {
        if (!DEBUG_MODE) return;

        this.text = layer.createText(
            LAYERS.DEFAULT,
            this.prefix + '0',
            FPS_CONFIG.x,
            FPS_CONFIG.y,
        ).attr(
            'fill',
            this.fill,
        );
    }

    update () {
        if (!DEBUG_MODE) return;

        this.text.attr(
            'text',
            this.prefix + (
                timeline.getFPS() >> 0
            ),
        );
    }
}


export default new FPS();
