import layer from '../layer.js';
import timeline from '../timeline.js';
import { MODES } from '../config-defaults.js';


class Displacement {
    image;
    anim  = {};
    anims = [];

    constructor ({ layer, imageSrc, width, height, startX, startY, endX, endY, animMap, duration }) {
        this.layer    = layer;
        this.imageSrc = imageSrc;
        this.width    = width;
        this.height   = height;
        this.startX   = startX;
        this.startY   = startY;
        this.endX     = endX;
        this.endY     = endY;
        if (typeof animMap === 'function') {
            this.anim.show = this.anim.hide = animMap;
        } else {
            this.anim = animMap;
        }
        this.duration = duration;
    }

    createTask (start, duration, sx, sy, ex, ey, anim, mode) {
        timeline.createTask({
            start       : start,
            duration    : duration,
            object      : this,
            data        : [ sx, sy, ex, ey, anim, mode ],
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    set () {
        this.image = layer.createImage(
            this.layer,
            this.imageSrc,
            this.startX,
            this.startY,
            this.width,
            this.height,
        );
    }

    show (start) {
        this.createTask(
            start,
            this.duration,
            this.startX,
            this.startY,
            this.endX,
            this.endY,
            this.anim.show,
            MODES.SHOW,
        );
    }

    hide () {
        this.anims.clear();
        this.createTask(
            0,
            this.duration,
            this.endX,
            this.endY,
            this.startX,
            this.startY,
            this.anim.hide,
            MODES.HIDE,
        );
    }

    onTimeUpdate (time, sx, sy, ex, ey, anim) {
        this.image.attr({
            x: anim(time, sx, ex - sx, this.duration),
            y: anim(time, sy, ey - sy, this.duration),
        });
    }

    onTimeStart () {
    }

    onTimeEnd (sx, sy, ex, ey, anim) {
        if (anim === MODES.HIDE) this.image.hide();
    }
}


class DisplacementFactory {
    create (config) {
        return new Displacement(config);
    }
}


export default new DisplacementFactory();
