import buzz from '../libs/buzz.js';
import soundButton from '../object/sound-button.js';


class Sound {
    constructor (src) {
        this.sound = new buzz.sound(src, { formats: [ "ogg", "mp3" ], preload: true, autoload: true, loop: false });
    }

    play () {
        this.sound.setPercent(0);
        this.sound.setVolume(100);
        soundButton.isOn && this.sound.play();
    }

    stop () {
        if (soundButton.isOn) {
            this.sound.fadeOut(1000, function () {
                this.pause();
            });
        } else {
            this.sound.stop();
        }
    }
}

class SoundFactory {
    create (src) {
        return new Sound(src);
    }
}

export default new SoundFactory();
