import {
    LAYERS, BRAND_QUESTION_TEXT1_CONFIG, BRAND_QUESTION_TEXT2_CONFIG, MODES, BRAND_ANSWER1_CONFIG, BRAND_ANSWER2_CONFIG,
    SCENES, MESSAGES,
} from '../config-defaults.js';
import message from '../message.js';
import layer from '../layer.js';
import tween from '../libs/tween/index.js';
import timeline from '../timeline.js';
import vibration from './vibration.js';


class BrandQuestion {
    setTimeout = timeline.setTimeout.bind(timeline);
    anim       = tween.exponential.co;
    anims      = [];
    text1;
    text2;
    text3;
    text4;
    answer1;
    answer2;

    set () {
        BRAND_QUESTION_TEXT1_CONFIG.textValue && (
            this.text1 = layer.createText(
                LAYERS.BRAND_QUESTION,
                BRAND_QUESTION_TEXT1_CONFIG.textValue,
                BRAND_QUESTION_TEXT1_CONFIG.textStartX,
                BRAND_QUESTION_TEXT1_CONFIG.textY,
                BRAND_QUESTION_TEXT1_CONFIG.textFill,
                BRAND_QUESTION_TEXT1_CONFIG.textSize,
                BRAND_QUESTION_TEXT1_CONFIG.textWeight,
                BRAND_QUESTION_TEXT1_CONFIG.textAnchor,
            ).hide()
        );

        this.text2 = layer.createText(
            LAYERS.BRAND_QUESTION,
            BRAND_QUESTION_TEXT2_CONFIG.textValue,
            BRAND_QUESTION_TEXT2_CONFIG.textStartX,
            BRAND_QUESTION_TEXT2_CONFIG.textY,
            BRAND_QUESTION_TEXT2_CONFIG.textFill,
            BRAND_QUESTION_TEXT2_CONFIG.textSize,
            BRAND_QUESTION_TEXT2_CONFIG.textWeight,
            BRAND_QUESTION_TEXT2_CONFIG.textAnchor,
        ).hide();

        this.answer1 = layer.createImage(
            LAYERS.BRAND_QUESTION,
            BRAND_ANSWER1_CONFIG.image,
            BRAND_ANSWER1_CONFIG.buttonX,
            BRAND_ANSWER1_CONFIG.buttonStartY,
            BRAND_ANSWER1_CONFIG.width,
            BRAND_ANSWER1_CONFIG.height,
        ).hide();

        this.text3 = layer.createText(
            LAYERS.BRAND_QUESTION,
            BRAND_ANSWER1_CONFIG.textValue,
            BRAND_ANSWER1_CONFIG.textX,
            BRAND_ANSWER1_CONFIG.textStartY,
            BRAND_ANSWER1_CONFIG.textFill,
            BRAND_ANSWER1_CONFIG.textSize,
            BRAND_ANSWER1_CONFIG.textWeight,
            BRAND_ANSWER1_CONFIG.textAnchor,
        ).hide();

        this.answer2 = layer.createImage(
            LAYERS.BRAND_QUESTION,
            BRAND_ANSWER2_CONFIG.image,
            BRAND_ANSWER2_CONFIG.buttonX,
            BRAND_ANSWER2_CONFIG.buttonStartY,
            BRAND_ANSWER2_CONFIG.width,
            BRAND_ANSWER2_CONFIG.height,
        ).hide();

        this.text4 = layer.createText(
            LAYERS.BRAND_QUESTION,
            BRAND_ANSWER2_CONFIG.textValue,
            BRAND_ANSWER2_CONFIG.textX,
            BRAND_ANSWER2_CONFIG.textStartY,
            BRAND_ANSWER2_CONFIG.textFill,
            BRAND_ANSWER2_CONFIG.textSize,
            BRAND_ANSWER2_CONFIG.textWeight,
            BRAND_ANSWER2_CONFIG.textAnchor,
        ).hide();

        this.group1 = layer.getLayer(LAYERS.RESULT_SCREEN).set();
        this.group1.push(this.answer1, this.text3);
        this.group2 = layer.getLayer(LAYERS.RESULT_SCREEN).set();
        this.group2.push(this.answer2, this.text4);

        this.group1.click(() => {
            vibration.vibrate();
            message.postMessage(SCENES.HOME_MENU, MESSAGES.SCENE_SWITCH_SCENE);
        });

        this.group2.click(() => {
            vibration.vibrate();
            message.postMessage(SCENES.HOME_MENU, MESSAGES.SCENE_SWITCH_SCENE);
        });

        layer.getLayer(LAYERS.BRAND_QUESTION).set()
        layer.hideLayer(LAYERS.BRAND_QUESTION);
    }

    show (start) {
        timeline.createTask({
            start       : start,
            duration    : BRAND_QUESTION_TEXT1_CONFIG.animLength,
            data        : [
                MODES.SHOW,
                BRAND_QUESTION_TEXT1_CONFIG.textStartX,
                BRAND_QUESTION_TEXT1_CONFIG.textEndX,
                BRAND_QUESTION_TEXT2_CONFIG.textStartX,
                BRAND_QUESTION_TEXT2_CONFIG.textEndX,
                BRAND_ANSWER1_CONFIG.textStartY,
                BRAND_ANSWER1_CONFIG.textEndY,
                BRAND_ANSWER1_CONFIG.buttonStartY,
                BRAND_ANSWER1_CONFIG.buttonEndY,
                BRAND_ANSWER2_CONFIG.textStartY,
                BRAND_ANSWER2_CONFIG.textEndY,
                BRAND_ANSWER2_CONFIG.buttonStartY,
                BRAND_ANSWER2_CONFIG.buttonEndY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    hide (start) {
        timeline.createTask({
            start       : start,
            duration    : BRAND_QUESTION_TEXT1_CONFIG.animLength,
            data        : [
                MODES.HIDE,
                BRAND_QUESTION_TEXT1_CONFIG.textEndX,
                BRAND_QUESTION_TEXT1_CONFIG.textStartX,
                BRAND_QUESTION_TEXT2_CONFIG.textEndX,
                BRAND_QUESTION_TEXT2_CONFIG.textStartX,
                BRAND_ANSWER1_CONFIG.textEndY,
                BRAND_ANSWER1_CONFIG.textStartY,
                BRAND_ANSWER1_CONFIG.buttonEndY,
                BRAND_ANSWER1_CONFIG.buttonStartY,
                BRAND_ANSWER2_CONFIG.textEndY,
                BRAND_ANSWER2_CONFIG.textStartY,
                BRAND_ANSWER2_CONFIG.buttonEndY,
                BRAND_ANSWER2_CONFIG.buttonStartY,
            ],
            object      : this,
            onTimeUpdate: this.onTimeUpdate,
            onTimeStart : this.onTimeStart,
            onTimeEnd   : this.onTimeEnd,
            recycle     : this.anims,
        });
    }

    onTimeUpdate (time, mode, t1sx, t1ex, t2sx, t2ex, at1sy, at1ey, ab1sy, ab1ey, at2sy, at2ey, ab2sy, ab2ey) {
        this.text1 && this.text1.attr('x', this.anim(time, t1sx, t1ex - t1sx, BRAND_QUESTION_TEXT1_CONFIG.animLength));
        this.text2 && this.text2.attr('x', this.anim(time, t2sx, t2ex - t2sx, BRAND_QUESTION_TEXT2_CONFIG.animLength));
        this.text3 && this.text3.attr('y', this.anim(time, at1sy, at1ey - at1sy, BRAND_ANSWER1_CONFIG.animLength));
        this.text4 && this.text4.attr('y', this.anim(time, at2sy, at2ey - at2sy, BRAND_ANSWER2_CONFIG.animLength));
        this.answer1 && this.answer1.attr('y', this.anim(time, ab1sy, ab1ey - ab1sy, BRAND_ANSWER1_CONFIG.animLength));
        this.answer2 && this.answer2.attr('y', this.anim(time, ab2sy, ab2ey - ab2sy, BRAND_ANSWER2_CONFIG.animLength));
    }

    onTimeStart (mode) {
        const group = [];

        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);
        if (this.text4) group.push(this.text4);
        if (this.answer1) group.push(this.answer1);
        if (this.answer2) group.push(this.answer2);

        if (mode === MODES.SHOW) {
            group.invoke(mode);
        }
    }

    onTimeEnd (mode) {
        const group = [];

        if (this.text1) group.push(this.text1);
        if (this.text2) group.push(this.text2);
        if (this.text3) group.push(this.text3);
        if (this.text4) group.push(this.text4);
        if (this.answer1) group.push(this.answer1);
        if (this.answer2) group.push(this.answer2);

        if (mode === MODES.HIDE) {
            group.invoke(mode);
        }
    }
}


export default new BrandQuestion();
